// Validation schemas
const createDeviceSchema = z.object({
  type: z.string().min(1),
  serialNumber: z.string().min(1).max(50),
  vehicleId: z.string().optional(),
  terminalId: z.string().optional(),
})

const createFirmwareVersionSchema = z.object({
  version: z.string().min(1),
  changelog: z.string().min(1),
  downloadUrl: z.string().url(),
  fileSize: z.number().min(1),
  checksum: z.string().min(1),
  isMandatory: z.boolean(),
})

type CreateDeviceFormData = z.infer<typeof createDeviceSchema>
type CreateFirmwareVersionFormData = z.infer<typeof createFirmwareVersionSchema>

// Sub-components
const DashboardCard = ({ title, value, icon: Icon, trend, color }: any) => {
  const colorClasses: any = {
    blue: 'from-blue-500 to-blue-600',
    green: 'from-green-500 to-green-600',
    purple: 'from-purple-500 to-purple-600',
    orange: 'from-orange-500 to-orange-600',
    red: 'from-red-500 to-red-600',
    cyan: 'from-cyan-500 to-cyan-600',
    amber: 'from-amber-500 to-amber-600',
    pink: 'from-pink-500 to-pink-600',
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      className="glass-panel p-6"
    >
      <div className="flex items-start justify-between">
        <div>
          <p className="text-sm text-slate-500 dark:text-slate-400">{title}</p>
          <h3 className="text-2xl font-bold mt-1">{value}</h3>
          {trend && (
            <p className={`text-sm mt-2 ${trend.startsWith('+') ? 'text-green-500' : 'text-red-500'}`}>
              {trend}%
            </p>
          )}
        </div>
        <div className={`w-12 h-12 rounded-xl bg-gradient-to-br ${colorClasses[color]} flex items-center justify-center`}>
          <Icon className="w-6 h-6 text-white" />
        </div>
      </div>
    </motion.div>
  )
}

