const CreateDeviceModal = ({ onClose, onSubmit, isSubmitting, methods }: any) => {
  const { t } = useTranslation()
  return (
    <div className="fixed inset-0 bg-black/50 backdrop-blur-sm z-50 flex items-center justify-center p-4">
      <motion.div
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        className="glass-panel w-full max-w-lg p-6 bg-white dark:bg-dark-800"
      >
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-xl font-display font-bold text-slate-900 dark:text-slate-100">
            {t('mdm.createDevice')}
          </h2>
          <button onClick={onClose} className="p-2 hover:bg-slate-100 dark:hover:bg-dark-700 rounded-lg">
            <X className="w-5 h-5" />
          </button>
        </div>

        <form onSubmit={methods.handleSubmit(onSubmit)} className="space-y-4">
          <div>
            <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('mdm.deviceType')}</label>
            <input {...methods.register('type')} className="input-field" placeholder="P18Q" />
            {methods.formState.errors.type && <p className="mt-1 text-sm text-error">{methods.formState.errors.type.message}</p>}
          </div>
          <div>
            <label className="block text-sm text-slate-500 dark:text-slate-400 mb-2">{t('mdm.serialNumber')}</label>
            <input {...methods.register('serialNumber')} className="input-field" placeholder="SN001" />
            {methods.formState.errors.serialNumber && <p className="mt-1 text-sm text-error">{methods.formState.errors.serialNumber.message}</p>}
          </div>
          <div className="flex gap-3 mt-6">
            <button type="button" onClick={onClose} className="flex-1 btn-secondary">
              {t('common.cancel')}
            </button>
            <button
              type="submit"
              disabled={isSubmitting}
              className="flex-1 btn-primary flex items-center justify-center gap-2 disabled:opacity-50"
            >
              {isSubmitting && <Loader2 className="w-4 h-4 animate-spin" />}
              {t('common.confirm')}
            </button>
          </div>
        </form>
      </motion.div>
    </div>
  )
}

