const RemoteControl = () => {
  const { t } = useTranslation()
  const diagnosticsMutation = useRemoteDiagnostics()
  const [targetId, setTargetId] = useState('')
  const [showConfigPanel, setShowConfigPanel] = useState(false)
  const [hotConfig, setHotConfig] = useState({
    heartbeatInterval: 30,
    logLevel: 'INFO',
    blacklistSyncInterval: 60,
  })

  const handleCommand = async (command: 'REBOOT' | 'LOG_TRANSFER' | 'WAKE_UP') => {
    if (!targetId) return
    try {
      await diagnosticsMutation.mutateAsync({ id: targetId, command })
    } catch (err) {
      console.error(err)
    }
  }

  const handleApplyConfig = () => {
    // TODO: Implement hot config API call
    setShowConfigPanel(false)
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="space-y-6"
    >
      <div>
        <h1 className="text-2xl font-display font-bold text-slate-900 dark:text-slate-100">{t('devices.remote')}</h1>
        <p className="text-slate-500 dark:text-slate-400 mt-1">{t('devices.remoteSubtitle')}</p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="lg:col-span-2 glass-panel p-6">
          <h3 className="text-lg font-semibold mb-6 flex items-center gap-2">
            <Terminal className="w-5 h-5 text-primary-500" />
            {t('mdm.sendCommand')}
          </h3>

          <div className="space-y-6">
            <div>
              <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-2">
                {t('mdm.deviceId')}
              </label>
              <input
                type="text"
                value={targetId}
                onChange={(e) => setTargetId(e.target.value)}
                placeholder="e.g. TERM-1001"
                className="input-field max-w-md"
              />
            </div>

            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4">
              {[
                { cmd: 'REBOOT', label: t('mdm.cmdRestart'), icon: RotateCcw, color: 'red' },
                { cmd: 'LOG_TRANSFER', label: t('mdm.cmdCheckHealth'), icon: HardDrive, color: 'blue' },
                { cmd: 'WAKE_UP', label: t('mdm.cmdSyncTime'), icon: Zap, color: 'amber' },
                { cmd: 'CLEAR_CACHE', label: t('mdm.cmdClearCache'), icon: Trash2, color: 'purple' },
                { cmd: 'SCREENSHOT', label: t('mdm.cmdScreenshot'), icon: Eye, color: 'cyan' },
                { cmd: 'LOCK_DEVICE', label: t('mdm.cmdLockDevice'), icon: Shield, color: 'slate' },
              ].map((item) => (
                <button
                  key={item.cmd}
                  disabled={!targetId || diagnosticsMutation.isPending}
                  onClick={() => handleCommand(item.cmd as any)}
                  className="p-4 rounded-xl border border-slate-200 dark:border-dark-600 hover:border-primary-500 dark:hover:border-primary-500 transition-all text-center group disabled:opacity-50"
                >
                  <div className={`w-10 h-10 mx-auto rounded-lg bg-${item.color}-500/10 flex items-center justify-center mb-3 group-hover:scale-110 transition-transform`}>
                    <item.icon className={`w-5 h-5 text-${item.color}-500`} />
                  </div>
                  <span className="font-medium text-sm text-slate-900 dark:text-slate-100">{item.label}</span>
                </button>
              ))}
            </div>

            {diagnosticsMutation.isSuccess && (
              <div className="p-3 bg-green-500/10 border border-green-500/20 rounded-lg text-green-600 text-sm">
                {t('common.saveSuccess')}
              </div>
            )}
          </div>
        </div>

        {/* Hot Config Panel */}
        <div className="glass-panel p-6">
          <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
            <Settings className="w-5 h-5 text-purple-500" />
            {t('mdm.remote.hotConfig')}
          </h3>
          <div className="space-y-4">
            <div>
              <label className="text-sm font-medium">{t('mdm.remote.heartbeatInterval')}</label>
              <div className="flex items-center gap-2 mt-1">
                <input
                  type="number"
                  value={hotConfig.heartbeatInterval}
                  onChange={e => setHotConfig({ ...hotConfig, heartbeatInterval: +e.target.value })}
                  className="input-field w-20"
                />
                <span className="text-sm text-slate-500">{t('time.second')}</span>
              </div>
            </div>
            <div>
              <label className="text-sm font-medium">{t('mdm.remote.logLevel')}</label>
              <select
                value={hotConfig.logLevel}
                onChange={e => setHotConfig({ ...hotConfig, logLevel: e.target.value })}
                className="input-field mt-1"
              >
                <option value="DEBUG">DEBUG</option>
                <option value="INFO">INFO</option>
                <option value="WARN">WARN</option>
                <option value="ERROR">ERROR</option>
              </select>
            </div>
            <div>
              <label className="text-sm font-medium">{t('mdm.remote.blacklistSync')}</label>
              <div className="flex items-center gap-2 mt-1">
                <input
                  type="number"
                  value={hotConfig.blacklistSyncInterval}
                  onChange={e => setHotConfig({ ...hotConfig, blacklistSyncInterval: +e.target.value })}
                  className="input-field w-20"
                />
                <span className="text-sm text-slate-500">{t('time.second')}</span>
              </div>
            </div>
            <button
              className="w-full btn-primary mt-4"
              disabled={!targetId}
              onClick={handleApplyConfig}
            >
              {t('mdm.remote.applyConfig')}
            </button>
          </div>
        </div>
      </div>

      {/* Command History */}
      <div className="glass-panel p-6">
        <h3 className="text-lg font-semibold mb-4 flex items-center gap-2">
          <Clock className="w-5 h-5 text-slate-500" />
          {t('mdm.commandHistory')}
        </h3>
        <div className="text-center py-8 text-slate-500">
          <p className="text-sm">{t('common.comingSoon')}</p>
        </div>
      </div>
    </motion.div>
  )
}

